import { Component, NgZone, ChangeDetectorRef  } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { HistitemsPage } from '../histitems/histitems';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@Component({
  selector: 'page-history',
  templateUrl: 'history.html',
})
export class HistoryPage {

  hist : any;
  usr : any = 0;
  constructor(public navCtrl: NavController, public navParams: NavParams, private zone: NgZone, public cdr: ChangeDetectorRef, public connect: Connect, private iab : InAppBrowser) {
     this.hist = [];
     this.usr = window.localStorage.getItem('userTag');
     
  }

  ionViewDidLoad() {
     this.hist = [];
     if (this.usr) 
      if (parseInt(this.usr)>0) {
        let that = this;
        let url = 'gethist.php?user='+this.usr;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.hist = data;
              that.cdr.markForCheck();             
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 
      }
  }

  showTrack(url) {
   if (url.length) {
      const browser = this.iab.create(url,'_blank','location=no');
   }
  }

  gotoTrans(h) {
    this.navCtrl.push( HistitemsPage, { order: h.id });
  }
}



