import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Tandc2Page } from '../tandc2/tandc2';

/**
 * Generated class for the TandcPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tandc',
  templateUrl: 'tandc.html',
})
export class TandcPage {

 public hlist : any[];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.hlist=[];
  }

  ionViewDidLoad() {
     var h = ['Payments & Disputes','Deliveries & Queries','Returns Policy','Privacy Policy','General Terms & Conditions'];
     var cnt=0;
     for (var i in h) {
        var ar={'num' : cnt++, 'name' : h[i]};
        this.hlist.push(ar);
     } 
  }

  goPage(a) {
     this.navCtrl.push(Tandc2Page, { 'page' : a.num, 'heading' : a.name });
  }

}



