import { Component, ViewChild, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, Navbar, Platform, LoadingController, ViewController } from 'ionic-angular';
import { EnterAddressPage } from '../enter-address/enter-address';
import { ShippingPage } from '../shipping/shipping';
import { Connect } from '../../providers/connect';

@IonicPage()
@Component({
  selector: 'page-choose-address',
  templateUrl: 'choose-address.html'
})

export class ChooseAddressPage {

  @ViewChild('navbar') navBar: Navbar;

  public methods: any[];

  constructor(public navCtrl: NavController, public navParams: NavParams, platform: Platform, public connect: Connect, public view: ViewController, private zone: NgZone, public cdr: ChangeDetectorRef, public loadingCtrl: LoadingController) {
     this.methods = [];
  }

  loadAddress() {
    let that=this;
    this.connect.getList('getaddress.php?tag=' + window.localStorage.getItem('userTag')).subscribe(
      data => {
        that.zone.run(() => {
          console.log(data);
          that.methods = data.methods;
          that.cdr.markForCheck();
        });
      },
      err => this.connect.logError(err)
    );
  }

  ionViewDidEnter() {

    this.loadAddress();
  }

  showEnterAddress() {
    this.navCtrl.push(EnterAddressPage);
  }

  closeme() {
    this.navCtrl.pop();
  }

  returnToMain() {
    this.navCtrl.pop();
  }

  showShipping(m)
  {
      this.navCtrl.push(ShippingPage, { addressid : m.id });
  }
}



