import { Component, NgZone, ChangeDetectorRef  } from '@angular/core';
import { IonicPage, NavController, ViewController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@IonicPage()
@Component({
  selector: 'page-enter-address',
  templateUrl: 'enter-address.html'
})
export class EnterAddressPage {

  public donealready : boolean = false;
  public mprofile : any = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, public view: ViewController, public connect: Connect, private zone: NgZone, public cdr: ChangeDetectorRef, public loadingCtrl: LoadingController, public alertCtrl: AlertController) {
     this.mprofile = { add1 : '', add2 : '', suburb : '', state : '', postcode : '', country : 'Australia', 'dflt' : false };
  }

  getToken() {

    if (!this.donealready) {
      this.donealready=true;

    let loader = this.loadingCtrl.create({
      content: "Please wait..."
    });
    loader.present();

   let that = this;

        this.connect.getList('newadd.php?tag='+window.localStorage.getItem('userTag')+'&add='+JSON.stringify(this.mprofile)).subscribe(
         data => {
	     that.zone.run(() => {
		 setTimeout(() => {
		        loader.dismiss();
		        that.navCtrl.pop();
	         }, 200);
                 that.cdr.markForCheck();
             });

         },
         err => {
          this.connect.logError(err);
	     that.zone.run(() => {
		 setTimeout(() => {
		        loader.dismiss();
                        that.navCtrl.pop();
	         }, 200);
                 that.cdr.markForCheck();
             });
         }
        );
     }
  }

  ionViewDidLoad() {

  }

  closeme()
  {
    this.navCtrl.pop();
  }

   showAlert(msg) {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'error',
      subTitle: msg,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }
}



