import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams, AlertController, MenuController, LoadingController, ModalController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { OneitemPage } from '../oneitem/oneitem';
import { MultiplePage } from '../multiple/multiple';
import { AccountPage } from '../account/account';
import { SpecialsPage } from '../specials/specials';
import { AddressPage } from '../address/address';
import { ConcernPage } from '../concern/concern';
import { HistoryPage } from '../history/history';
import { HandyPage } from '../handy/handy';
import { MapPage } from '../map/map';
import { TandcPage } from '../tandc/tandc';
import { CheckoutPage } from '../checkout/checkout';
import { PaymentsPage } from '../payments/payments';
import { MyaccountPage } from '../myaccount/myaccount';
import { ChooseAddressPage } from '../choose-address/choose-address';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

@Component({
  selector: 'page-main',
  templateUrl: 'main.html',
})
export class MainPage {

 public orderitems: any[];
  myPic : any = './assets/img/blankbanner.jpg';

 public total : any = 0;
 public itemcount : any = 0;
 public orderTotal : any=0;
 public freightAmount : any=0;
 public freightValid: boolean = false;
 public userPostcode : any = '';
 public mytext : any = '';
  constructor(public nav: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public modalCtrl: ModalController, public menuCtrl: MenuController, private connect: Connect, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef, public barcodeScanner : BarcodeScanner) {
    this.orderitems = [];
    this.mytext='';
  }

  recalc()
  { if (window.localStorage.getItem('freightValid')) 
      this.freightValid = true;
    else 
      this.freightValid = false;

    if (window.localStorage.getItem('userPostcode'))
      this.userPostcode = window.localStorage.getItem('userPostcode');
    else
      this.userPostcode='';
    
    this.orderitems = [];
       this.total=0;
    this.itemcount=0;
    var c = window.localStorage.getItem('cart');
    if (!c) {
      c='';
      window.localStorage.setItem('cart',"");
    }
    if (c.length) {
       this.menuCtrl.enable(true,'mainmenu'); 
       var i=c.split('~');
       for (var e=0; e<i.length; e++) {
         var f=i[e].split('^');
         var ths = { id: f[0], qty: f[1], pic: 'http://theparcelpeople.com.au/catalogue/pics/'+f[0]+'.jpg', title: f[3], price: f[2], company: f[4]};
         this.orderitems.push(ths);
         this.total += parseFloat(f[1]) * parseFloat(f[2]);
         this.itemcount += parseInt(f[1]);
         this.myPic = 'http://theparcelpeople.com.au/catalogue/banners/'+f[4]+'.jpg?v=3';
       }
       this.orderTotal = this.total + this.freightAmount;
    }
  }

  ionViewDidEnter()  {
     window.localStorage.removeItem('freightValid');
     this.recalc();
  }
 

  doSpecials()
  {
         let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Searching..."
	    });
	    loader.present();

        let url = 'getspecials.php?cart='+window.localStorage.getItem('cart');
// console.log(url);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.mytext='';
           that.zone.run(() => {
                  window.localStorage.removeItem('toadd');
		    this.nav.push(SpecialsPage,{items: data.items});
              
           });
         },
         err => {
            loader.dismiss();
            that.mytext='';
            this.connect.logError(err)
         }
        ); 
  }

  concern() {
     var usr = window.localStorage.getItem('userTag');
     if (!usr)           
       usr='';
     if (usr.length) 
       this.nav.push( ConcernPage);
     else {
      let alert = this.alertCtrl.create({
      title: 'Please Log In',
      subTitle: 'Please go to the Your Account menu item to Log In before you lodge a concern',
      buttons: [
        {
          text: 'Ok',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
     }
  }
 
  pleaseSignUpAlert() {
    let alert = this.alertCtrl.create({
      title: 'Join Tella',
      subTitle: 'Sign up to Tella and experience a new era in online shopping',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
           }
        },
        {
          text: 'Ok',
          handler: () => {
 //           this.nav.setRoot(HomePage);
          }
        }
      ]
    });
    alert.present();
  }

  showmodal() {
     let mdl = this.modalCtrl.create(HandyPage,{},{showBackdrop: true, enableBackdropDismiss: true});
     mdl.present();
  }

  showAccount() {
    this.nav.push(MyaccountPage);
  }

  calcShip() {
    let alert = this.alertCtrl.create({
      title: 'Calculate Shipping',
      subTitle: 'What is the PostCode where your order will be sent?',
      inputs: [
         { name : 'postcode',
           placeholder: this.userPostcode
         },
      ], 
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
           }
        },
        {
          text: 'Ok',
          role: 'cancel',
          handler: data => {
             this.doCalcShip(data.postcode);
          }
        }
      ]
    });
    alert.present();
  }

  doCalcShip(p)
  { if (!p.length) p=this.userPostcode;
    window.localStorage.setItem('userPostcode',p);
    if (p.length) {
       let that=this;
       let loader = this.loadingCtrl.create({
	      content: "Calculating..."
	    });
	    loader.present();

        let url = 'calcship.php?postcode='+p+'&cart='+window.localStorage.getItem('cart');
       console.log(url);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             console.log(JSON.stringify(data));
             if (data.success) {
                that.freightAmount = data.freight;
                window.localStorage.setItem('freightValid','1');
                that.recalc();
                that.cdr.markForCheck();
              }
             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        );
    }
  }

   noAvailable() {
    let alert = this.alertCtrl.create({
      title: 'Tella Message',
      subTitle: 'The item that you scanned is no longer available.  Please scan another code.',
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
           }
        }
      ]
    });
    alert.present();
  }

  conflictWarning() {
    let alert = this.alertCtrl.create({
      title: 'Tella Message',
      subTitle: 'The item that you scanned is not compatible with your current shopping cart.  Please complete your existing order before starting a new one.',
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
           }
        }
      ]
    });
    alert.present();
  }

    signOutAlert() {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
            that.doLogout();
          }
        }
      ]
    });
    alert.present();
  }

  doLogout()
  {
     window.localStorage.removeItem('userTag');
     window.localStorage.removeItem('cart');

  }

  doScan() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.checkCode(barcodeData.text);
       }
    }, (err) => {
    });
  }

  loadText()
  { 
 this.checkCode('*'+this.mytext);
  }

  checkCode(s)
  {
         let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Searching..."
	    });
	    loader.present();

        let url = 'getitems.php?code='+s+'&cart='+window.localStorage.getItem('cart');
// console.log(url);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.mytext='';
           that.zone.run(() => {
             switch (parseInt(data.number)) {
              case -1 : that.conflictWarning();
                     break;
              case 1 : 
		    this.nav.push(OneitemPage,{item: data.item, buttonmode: 'ADD TO CART'});
                  break;
              case 0 : 
                  that.noAvailable();
                  break;
              default: 
                    window.localStorage.removeItem('toadd');
		    this.nav.push(MultiplePage,{items: data.items});
             }
             
           });
         },
         err => {
            loader.dismiss();
            that.mytext='';
            this.connect.logError(err)
         }
        ); 
  }

  showProfile()
  {  let that = this;
     if (window.localStorage.getItem('userTag')) {
	    this.nav.push(AccountPage);
     }
     else
        this.pleaseSignUpAlert();
  }

  showPayments()
  {  let that = this;
     if (window.localStorage.getItem('userTag')) {
	    this.nav.push(PaymentsPage);              
     }
     else
        this.pleaseSignUpAlert();
  }

  showAddress()
  {  let that = this;
     if (window.localStorage.getItem('userTag')) {
	    this.nav.push(AddressPage);              
     }
     else
        this.pleaseSignUpAlert();
  }

  doMap()
  { this.nav.push(MapPage);
  }

  removeProduct(id)
  { var c = window.localStorage.getItem('cart');
    var i = c.split('~');
    var nw=[];
    for (var e=0; e<i.length; e++) {
      var a = i[e].split('^');
     if (a[0] != id)
        nw.push(i[e]); 
    }
    window.localStorage.setItem('cart',nw.join('~'));
    window.localStorage.removeItem('freightValid');
    this.recalc();
  }   

  addProduct(id)
  { var c = window.localStorage.getItem('cart');
    var i = c.split('~');
    var nw=[];
    for (var e=0; e<i.length; e++) {
      var a = i[e].split('^');
     if (a[0] != id)
        nw.push(i[e]); 
     else {
        var q=parseInt(a[1])+1;
        a[1]=q.toString();
        nw.push(a.join('^'));
     } 
    }
   window.localStorage.setItem('cart',nw.join('~'));
    window.localStorage.removeItem('freightValid');
    this.recalc();
  }

  subProduct(id)
  { 
    var c = window.localStorage.getItem('cart');
    var i = c.split('~');
    var nw=[];
    for (var e=0; e<i.length; e++) {
      var a = i[e].split('^');
     if (a[0] == id)
     {
        var q=parseInt(a[1])-1;
        if (q>0) a[1]=q.toString();
     } 
     nw.push(a.join('^'));
    }
    window.localStorage.setItem('cart',nw.join('~'));
    window.localStorage.removeItem('freightValid');
    this.recalc();
  }

 checkOut()
 { this.nav.push(CheckoutPage);
 }

 tandc()
 { this.nav.push(TandcPage);
 }
   
 goProd(m)
 { this.nav.push(OneitemPage,{item : m, buttonmode: ''});
 }

 orderHistory()
 { this.nav.push( HistoryPage);
 }

}



