import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Facebook, FacebookLoginResponse } from '@ionic-native/facebook';
import { EmailLoginPage } from '../email-login/email-login';
import { EnterAddressPage } from '../enter-address/enter-address';
import { RegisterPage } from '../register/register';
import { EnterCreditCardPage } from '../enter-credit-card/enter-credit-card';
import { TandcPage } from '../tandc/tandc';
import { Connect } from '../../providers/connect';


@IonicPage()
@Component({
  selector: 'page-checkout',
  templateUrl: 'checkout.html',
})
export class CheckoutPage {
 
  public user : any = '';
  public address : any = '';
  public username : any = '';
  public shipping : any = '';
  public payment : any = '';
  public paycard : any = '';
  public methods: any[];
  public pmethods: any[];
  public amode : any = 1;
  public pmode : any = 1;
  public reg: any ;
  public allok : boolean = false;
  public stripe_pub: any = '';

  FB_APP_ID: number = 1455239151263568;

  constructor(public navCtrl: NavController, public cdr: ChangeDetectorRef, public fb: Facebook, public alertCtrl: AlertController, public navParams: NavParams, public loadingCtrl: LoadingController, public connect: Connect, private zone: NgZone) {
    this.methods = [];
    this.amode=1;
    this.pmode = 1;
    this.reg = {
      id : '',
      firstname: '',
      surname: '',
      phone:'',
      postcode:''
    }

  }

  ionViewDidEnter() {
    this.recalc();
  }

  recalc() {
     this.user = window.localStorage.getItem('userTag');
     if (!this.user)           
       this.user='';
     if (!this.user.length) {
         window.localStorage.removeItem('userAddress');
         window.localStorage.removeItem('userPayment');
         window.localStorage.removeItem('userPostcode');
     }
     this.address = window.localStorage.getItem('userAddress');
     if (!this.address) 
       this.address='';
     this.payment = window.localStorage.getItem('userPayment');
     if (!this.payment)
       this.payment='';
     if ((this.address.length) && (this.payment.length)) 
        this.allok = true;
     if (this.user.length) {
         let that=this;
	    let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();
         this.connect.getList('getuserdetails.php?amode='+this.amode+'&pmode='+this.pmode+'&user='+this.user+'&address='+this.address+'&payment='+this.payment+'&cart='+window.localStorage.getItem('cart')).subscribe(
         data => {
           that.zone.run(() => {
             loader.dismiss();
             that.user = data.user;
             that.address = data.address;
             that.username = data.username;
             that.shipping = data.shipping;
             that.methods = data.methods;
             that.paycard = data.paycard;
             that.pmethods = data.pmethods;
             that.stripe_pub = data.stripe_pub;
             that.payment = data.payment;
             window.localStorage.setItem('userAddress',that.address);
             window.localStorage.setItem('userPayment',that.payment);
             window.localStorage.setItem('userPostcode',data.postcode);
	     if ((that.address.length) && (that.payment.length)) 
        	that.allok = true;
             else
                that.allok=false;
             that.cdr.markForCheck();
           });
         },
         err => { loader.dismiss();
                this.connect.logError(err);
         }
        ); 

     }     
  }

  myAlert(msg) {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: msg,
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }

  doLogout()
  {  window.localStorage.removeItem('userTag');
     this.recalc();     
  }

  loginUserWithFacebook() {
	let that=this;

	this.fb.login(['public_profile', 'email'])
	  .then((res: FacebookLoginResponse) => {
	    let loader = this.loadingCtrl.create({
	      content: "Please wait..."
	    });
	    loader.present();
            let url='facebook.php?user='+res.authResponse.userID+'&access='+res.authResponse.accessToken;
        //   this.myAlert(url);
            this.connect.getList(url).subscribe(
	         data => {
  		      loader.dismiss();
                    if ((data.firstname.length<1) || (data.surname.length<1) || (data.postcode.length<1)) {
                       that.reg.id=data.user;
                       that.reg.email = data.email;
                       that.reg.firstname=data.firstname;
                       that.reg.surname = data.surname;
                       that.reg.phone=data.phone;
                       that.reg.postcode = data.postcode;
                       that.amode=1;
                       that.pmode=1;
                       that.navCtrl.push( RegisterPage, { reg : that.reg });
                    }  
                    else {
                       window.localStorage.setItem('userTag',data.id);
                       that.recalc();
                    }
 	         },
	         err => {
                    loader.dismiss();
                    this.connect.logError(err);
                 }
	        );
           })
	  .catch(e => this.myAlert('Error logging into Facebook' + JSON.stringify(e)));
  }
 
  openSignUpPage() {
    this.navCtrl.push(EmailLoginPage);
    this.amode=1;
    this.pmode=1;
  }

  showAddress(a)
  { window.localStorage.setItem('userAddress',a);
    this.amode=0;
    this.pmode=0;
    this.recalc();
  }
  
  showEnterAddress() {
    this.navCtrl.push(EnterAddressPage);
  }

  showPayment(a)
  { window.localStorage.setItem('userPayment',a);
    this.pmode=0;
    this.recalc();
  }
  
  showEnterPayment() {
    this.navCtrl.push(EnterCreditCardPage, {stripe_pub : this.stripe_pub});
  }

  doConfirm() {
            let that = this;
	    let loader = this.loadingCtrl.create({
	      content: "Please wait..."
	    });
	    loader.present();
            let url='saveorder.php?user='+window.localStorage.getItem('userTag')+'^'+this.address+'^'+this.payment+'&cart='+window.localStorage.getItem('cart');
          //  this.myAlert(url);
             this.connect.getList(url).subscribe(
	         data => {
		    loader.dismiss();
                    if (data.success) 
                      this.orderComplete();
                    else
                      this.myAlert('Your order could not be completed.  Please try again later');
                 },
	         err => {
                    loader.dismiss();
                    this.connect.logError(err);
                 }
	        );


  }

  openTermsAndConditions() {
    this.navCtrl.push(TandcPage);
  }


  orderComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Your order has been completed successfully',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             window.localStorage.removeItem('cart');
             that.navCtrl.pop(); 
          }
        }
      ]
    });
    alert.present();
  }

}



