import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ViewController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import {OneitemPage} from '../oneitem/oneitem';

@IonicPage()
@Component({
  selector: 'page-specials',
  templateUrl: 'specials.html',
})
export class SpecialsPage {

  public anychosen : boolean = false;
  public items: any[] = [];
  public bannerImg : any = './assets/img/blankbanner.jpg';
  constructor(public nav: NavController, public navParams: NavParams, public view : ViewController, private connect: Connect, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef) {
      this.items=this.navParams.get('items');
  }

  ionViewDidEnter() {
    this.recalc();
  }

  recalc() {
   var c = window.localStorage.getItem('toadd');
    if (!c) c='';
    this.anychosen = false;
      for (var ss in this.items) {
        if (this.items[ss].id == c)
          this.items[ss].added = 1;
        if (this.items[ss].added == 1)
           this.anychosen = true;
      }
 //   console.log('any='+this.anychosen);
  }

  AddToCart()
 { var c = window.localStorage.getItem('cart');
   if (!c) c='';
   for (var ss in this.items)
       if (this.items[ss].added>0) 
         c= "" + this.items[ss].id+"^1^"+this.items[ss].price+"^"+this.items[ss].title+"^"+this.items[ss].company + (c.length ? '~' : '') + c;
   window.localStorage.setItem('cart',c);      
   this.nav.pop();
 }

 selectProduct(pos)
 { for (var ss in this.items)
    if (this.items[ss].pos == pos) 
      this.items[ss].added = 1;
   this.anychosen = true;
 }

 unselectProduct(pos)
 { this.anychosen = false;
   for (var ss in this.items) {
     if (this.items[ss].pos == pos) 
        this.items[ss].added =0;
     if (this.items[ss].added==1) 
       this.anychosen = true;
   }
 }

 goProduct(item)
 { 
    window.localStorage.removeItem('toadd');
    this.nav.push(OneitemPage,{item: item, buttonmode: (item.added==0 ? 'SELECT' : '')});
  }

 closeme()
 { this.nav.pop();
 }

}



