import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@IonicPage()
@Component({
  selector: 'page-conversation',
  templateUrl: 'conversation.html',
})
export class ConversationPage {

  public concern : any = [];

  public newmode : any;
  public clist : any = [];
  public cid : any =0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public alertCtrl: AlertController, private connect: Connect) {
     this.concern = { date : '', info : ''};
     this.newmode = 0;
     this.clist = [];
     this.cid = this.navParams.get('id');
  }

  ionViewDidLoad() {

           let that=this;
           let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();

        let url = 'getconv.php?id='+this.cid+'&rnd='+Math.random();
        this.connect.getList(url).subscribe(
         data => {
            loader.dismiss();
            that.clist = data.list;
         },
         err => {
            loader.dismiss();
         }
        ); 
  }

  sendConcern() {
           let that=this;
           let loader = this.loadingCtrl.create({
	      content: "Sending..."
	    });
	    loader.present();

        let url = 'sendconcern2.php?user='+window.localStorage.getItem('userTag')+'&cid='+this.cid+'&info='+encodeURIComponent(this.concern.info);
        this.connect.getList(url).subscribe(
         data => {
            loader.dismiss();
            that.concern.info='';
            that.concern.date='';
            let alert = this.alertCtrl.create({
            title: 'New Message Sent',
      subTitle: 'We will respond as soon as possible',
      buttons: [
       
        {
          text: 'Ok',
          handler: () => {
           this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
   
        },
         err => {
            loader.dismiss();
         }
        ); 
  }
}



