import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { RegisterPage } from '../register/register';

/**
 * Generated class for the EmailLoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-email-login',
  templateUrl: 'email-login.html',
})
export class EmailLoginPage {

  error: any;
  form: any;
  reg: any;
  newmode : any = '0';

  constructor(public loadingCtrl: LoadingController, public navCtrl: NavController, public toastCtrl : ToastController, public connect: Connect, public navParams: NavParams) {
    this.newmode = '0';
    this.form = {
      email: '',
      password: ''
    }
    this.reg = {
      id : '',
      firstname: '',
      surname: '',
      phone:'',
      postcode:''
    }
    
  }

  private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }

  ionViewDidEnter() {
    if (window.localStorage.getItem('userTag'))
      this.navCtrl.pop();
  }

  login() {
    var msg='';
    if (this.form.password.length<6)
      msg='Password Too Short';
  
    var a1 = this.form.email.indexOf('@');
    if (a1>0) {
      var m = this.form.email.substr(a1);
      if (m.indexOf('.')<=0) 
        msg='Invalid Email Address';
    }
    else msg='Invalid Email Address';
    if (msg.length) 
      this.presentToast(msg);
    else
     this.dologin();
  }

  dologin() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();
    let that = this;
    this.connect.getList('userlogin.php?params='+encodeURI(this.form.email+'^'+this.form.password)).subscribe(
          data => {
            loading.dismiss();
            if (data.success > '0') {
              if ((data.firstname.length > 0) && (data.surname.length>0)) {  
  	        window.localStorage.setItem('userTag',data.userTag);
               that.navCtrl.pop();
              }
              else {
               that.reg.id=data.userTag;
               that.newmode = '1';
               that.reg.email = data.email;
               that.reg.firstname=data.firstname;
               that.reg.surname = data.surname;
               that.reg.phone = data.phone;
               that.reg.postcode = data.postcode;
               that.navCtrl.push(RegisterPage,{reg:that.reg});
              }
            }
            else {
                that.error = data.error; 
            }
          },
          err => this.connect.logError(err)
        );
  }

}






