import { Component, ViewChild, ElementRef, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { Connect } from '../../providers/connect';
import { Geolocation } from '@ionic-native/geolocation';


declare var google;

@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {

  @ViewChild('map') mapElement: ElementRef;
  map: any;
  markers: any[];
  dinfo: any[];
  infowindow: any;
  address: any;
  latitude: any;
  longitude: any;
  formatedAddress: any;
  public lat :  string = '';
  public lng : string = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public connect : Connect, public geolocation: Geolocation, private zone: NgZone, public cdr: ChangeDetectorRef) {
     this.markers = [];
     this.dinfo = [];
  }

  ionViewDidLoad() {
  this.latitude='-31.942957';
  this.longitude='115.827994';

  this.geolocation.getCurrentPosition().then((resp) => {
  if (resp.coords != undefined) {
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
  }
  this.loadMap();
}).catch((error) => {
   this.loadMap();
});


  }

 loadMap() {
    let location = new google.maps.LatLng(parseFloat(this.latitude), parseFloat(this.longitude));

    let mapOptions = {
      center: location,
      zoom: 15,
//      disableDefaultUI: true, //https://developers.google.com/maps/documentation/javascript/controls//
      mapTypeId: google.maps.MapTypeId.ROADMAP
    }


    this.map = new google.maps.Map(this.mapElement.nativeElement,mapOptions);

                  this.infowindow=new google.maps.InfoWindow({
                     content: 'nothing yet'
                  });


    let that=this;

   let url = 'getdeals.php';
   let cnt = 0;
   let img = 'http://jaydenkur.com.au/aserv/icon2.png';
        this.connect.getList2(url).subscribe(
         data => {
           that.zone.run(() => {
      	     for (var d in data) {
		    var mk = new google.maps.LatLng(parseFloat(data[d].lat), parseFloat(data[d].lng));
		    that.markers.push(new google.maps.Marker({
		          position: mk,
		          draggable: false,
		 //         animation: google.maps.Animation.DROP,
               //         icon: img,
		          map: this.map,
		          title: data[d].name
		        }));
                  that.dinfo.push('<div><b>'+data[d].name+'</b></div>'+data[d].address+'<br><i><b>'+data[d].deal+'</i></b>');


		 google.maps.event.addListener(that.markers[cnt], 'mouseover', function() {
                         let num=that.markers.indexOf(this);
                         that.infowindow.setContent(that.dinfo[num]);
                         that.infowindow.setPosition(that.markers[num].position);
                         that.infowindow.open(that.map);
                       });

                       google.maps.event.addListener(that.markers[cnt], 'mouseout', function() {
                          that.infowindow.close();
                       });


                  cnt++;
	     }	          
           });
           console.log('count='+cnt);
           console.log(that.markers);
           that.cdr.markForCheck();
 
         },
         err => {
            this.connect.logError(err)
         }
        ); 
  }

}



