import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { ConversationPage } from '../conversation/conversation';

@IonicPage()
@Component({
  selector: 'page-concern',
  templateUrl: 'concern.html',
})
export class ConcernPage {

  public concern : any = [];

  public newmode : any;
  public clist : any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public alertCtrl: AlertController, private connect: Connect) {
     this.concern = { date : '', info : ''};
     this.newmode = 0;
     this.clist = [];
  }

  ionViewDidLoad() {

           let that=this;
           let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();

        let url = 'getconcerns.php?user='+window.localStorage.getItem('userTag')+'&rnd='+Math.random();
        this.connect.getList(url).subscribe(
         data => {
            loader.dismiss();
            that.clist = data.list;
            if (that.clist.length) that.newmode=0;
            else that.newmode=1;
         },
         err => {
            loader.dismiss();
         }
        ); 
  }

  goConv(i) {
    this.navCtrl.push( ConversationPage, { id: i});
  }

  sendConcern() {
           let that=this;
           let loader = this.loadingCtrl.create({
	      content: "Sending..."
	    });
	    loader.present();

        let url = 'sendconcern.php?user='+window.localStorage.getItem('userTag')+'&date='+encodeURIComponent(this.concern.date)+'&info='+encodeURIComponent(this.concern.info);
        this.connect.getList(url).subscribe(
         data => {
            loader.dismiss();
            that.concern.info='';
            that.concern.date='';
            let alert = this.alertCtrl.create({
            title: 'Message Sent',
      subTitle: 'We will address your concern as soon as possible',
      buttons: [
       
        {
          text: 'Ok',
          handler: () => {
           this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
   
        },
         err => {
            loader.dismiss();
         }
        ); 
  }
}



