import { Component, ViewChild, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, Navbar, Platform, LoadingController, ViewController } from 'ionic-angular';
import { EnterCreditCardPage } from '../enter-credit-card/enter-credit-card';
import { Connect } from '../../providers/connect';

@IonicPage()
@Component({
  selector: 'page-payments',
  templateUrl: 'payments.html'
})

export class PaymentsPage {

  @ViewChild('navbar') navBar: Navbar;

  public methods: any[];
  public stripe_pub: any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, platform: Platform, public connect: Connect, public view: ViewController, private zone: NgZone, public cdr: ChangeDetectorRef, public loadingCtrl: LoadingController) {
  }

  delete(id, cardid) {
    let that=this;
    let loader = this.loadingCtrl.create({
      content: "Please wait..."
    });
    loader.present();

    this.connect.getList('deletecc.php?id=' + id + "&cardid=" + cardid + '&tag=' + window.localStorage.getItem('userTag')).subscribe(
      data => {
	   that.zone.run(() => {
             that.methods = data.methods;
	     loader.dismiss();
             that.cdr.markForCheck();
           });
      },
      err => this.connect.logError(err)
    );
  }

  loadCards() {
    let that=this;
    this.connect.getList('getcards.php?tag=' + window.localStorage.getItem('userTag')).subscribe(
      data => {
        that.zone.run(() => {
          console.log(data);
          that.methods = data.methods;
          that.stripe_pub = data.stripe_pub;
          that.cdr.markForCheck();
        });
      },
      err => this.connect.logError(err)
    );
  }

  makeDefault(id, cardid) {
    let that = this;
    let loader = this.loadingCtrl.create({
      content: "Please wait..."
    });
    loader.present();
    this.connect.getList('setdefaultcard.php?id=' + id + "&cardid=" + cardid + '&tag=' + window.localStorage.getItem('userTag')).subscribe(
      data => {
	   that.zone.run(() => {
             that.methods = data.methods;
	     loader.dismiss();
             that.cdr.markForCheck();
           });
      },
      err => this.connect.logError(err)
    );
  }

  ionViewDidEnter() {

    this.loadCards();
  }

  showEnterCreditCard() {
    this.navCtrl.push(EnterCreditCardPage, {stripe_pub : this.stripe_pub});
  }

  closeme() {
    this.navCtrl.pop();
  }

  returnToMain() {
    this.navCtrl.pop();
  }
}



