import { Component, NgZone, ChangeDetectorRef  } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-tandc2',
  templateUrl: 'tandc2.html',
})

export class Tandc2Page {

  qns : any;
  page : any;
  public shownGroup : any;
  heading : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private zone : NgZone, public cdr : ChangeDetectorRef, public connect : Connect) {
     this.qns=[];
     this.shownGroup = null;
     this.page = this.navParams.get("page");
     this.heading = this.navParams.get("heading");
  }

  ionViewDidLoad() {
       let that = this;
        let url = 'gettandc.php?page='+this.page;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.qns = data;
              that.cdr.markForCheck();             
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 
  }

  toggleGroup(group) {
    if (this.isGroupShown(group)) {
        this.shownGroup = null;
    } else {
        this.shownGroup = group;
    }
};

isGroupShown(group) {
    return this.shownGroup === group;
};

}



