import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ActionSheetController, ViewController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@IonicPage()
@Component({
  selector: 'page-account',
  templateUrl: 'account.html',
})

export class AccountPage {

  public mprofile: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController,  public view : ViewController, public asc : ActionSheetController, public connect: Connect, public zone: NgZone, public cdr : ChangeDetectorRef) {
     this.mprofile = { id:0, firstName: '', lastName: '', email: '', gender: '', phone: ''};
  }


  ionViewDidLoad() {
    let that=this;
    this.connect.getList('accountdata.php?id='+window.localStorage.getItem('userTag')).subscribe(
      data => {
        that.zone.run(() => {
          that.mprofile = data.profile;
          console.log(that.mprofile);
          that.cdr.markForCheck();
        });
      },
      err => this.connect.logError(err)
    );

  }

  chooseGender() {
    let actionSheet = this.asc.create({
      title: 'Gender',
      buttons: [
        {
          text: 'Male',
          role: 'cancel',
          handler: () => {
          }
        },{
          text: 'Female',
          role: 'cancel',
          handler: () => {
          }
        },{
          text: 'Other',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    actionSheet.present();
  }


  saveAlert() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Your Profile has been updated',
      buttons: [
        {
          text: 'Okay',
          handler: () => {
            that.view.dismiss();
          }
        }
      ]
    });
    alert.present();
  }


infoAlert(title, message) {
  let alert = this.alertCtrl.create({
    title: title,
    message: message,
    buttons: ['Ok']
  });
  alert.present()
}


closeme()
{
  this.view.dismiss();

}

saveme()
{
  let that=this;
  let url = 'saveaccountdata.php?params='+JSON.stringify(this.mprofile);
  console.log(url);
  this.connect.getList(url).subscribe(
    data => {
    },
    err => this.connect.logError(err),
    () => {
      this.saveAlert();
    }
  );
}


}



