import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the OneitemPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-oneitem',
  templateUrl: 'oneitem.html',
})
export class OneitemPage {

  myPic : any = './assets/img/blank.jpg';
  bannerPic : any = './assets/img/blankbanner.jpg';
  item: any = { id: 0, title: '', pic: '', price: ''};
  title: any = '';
  price: any = '';
  qty: any = 0;
  details: any = '';
  buttonmode: any = 'Select';

  constructor(public navCtrl: NavController, public navParams: NavParams, public view: ViewController, private connect: Connect, private zone: NgZone, public cdr: ChangeDetectorRef) {
     this.item = this.navParams.get('item');
     this.buttonmode = this.navParams.get('buttonmode');
     this.details = 'Lorem emptor ...';
     this.title = this.item.title;
     this.myPic = this.item.pic;
     this.bannerPic = 'http://theparcelpeople.com.au/catalogue/banners/'+this.item.company+'.jpg?v=2';
     this.qty = this.item.qty;
     this.price = this.item.price;     
  }

  ionViewDidLoad() {
        let that=this;
        this.connect.getList('itemdetails.php?id='+this.item.id).subscribe(
         data => {
           that.zone.run(() => {
             that.details = data.details;
             that.cdr.markForCheck();
           });
         },
         err => this.connect.logError(err)
        );

  }

  closeme()
  { this.view.dismiss();
  }

 addProduct()
 { 
   if (this.buttonmode == 'SELECT') {
       window.localStorage.setItem('toadd',this.item.id);
// console.log('adding ' + this.item.id);
   }
   else {
    var c = window.localStorage.getItem('cart');
    var nw=[];
    var found = false;
    if (c.length) {
     var i=c.split('~');
     for (var e=0; e< i.length; e++) {
      var t = i[e].split('^');
      if (t[0] == this.item.id) {
         var tt=parseInt(t[1])+1;      
         t[1]=tt.toString();
         found = true;
      }
      nw.push(t.join('^'));
     }
    }
    if (!found) 
      nw.unshift(""+this.item.id+"^1^"+this.item.price+"^"+this.item.title+'^'+this.item.company);
    c=nw.join('~');
    window.localStorage.setItem('cart',c);      
   }
   this.closeme();
 }

}



